.welcome {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
}

.welcome_form {
    height: 80%;
    width: 350px;
    background-color: white;
    padding: 0 40px;
    border-radius: 10px;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.welcome_title {
    margin-bottom: 2px;
    color: #2e0e3d;
    font-weight: bold;
}