form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 5px;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

.form-actions {
    padding-top: 20px;
    display: flex;
    column-gap: 10px;
}

input {
    padding: 5px 10px;
}

.action-close {
    background-color: transparent;
    border: none;
    padding: 10px;
}

.action-close:hover {
    color: red;
}

.action-added {
    background-color: aqua;
    border: 1px solid black;
    padding: 10px;
}

.action-added:hover {
    background-color: aquamarine;
}