.btn_added {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 1px solid white;
    color: white;
    font-size: 24px;
    background-color: gray;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);

    cursor: pointer;

    position: fixed;
    right: 24px;
    bottom: 24px;

    transform: rotate(0);
    transition: 1s ease;

    z-index: 1;
}

.btn_added:hover {
    transform: rotate(360deg);
  }