.item {
    border: 1px solid black;
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    margin-bottom: 5px;
    font-weight: bold;
}

.item_icon {
    cursor: pointer;
}

.icon_check:hover {
    color: green;
}

.icon_close:hover {
    color: red;
}

.icon_check-active {
    color: green;
}

.text-through {
    text-decoration: line-through;
}